/* --- RESET & BASE THEME --- */
html, body {
  margin: 0;
  padding: 0;
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
  background: #f4f7fb;
  min-height: 100vh;
  color: #1a2233;
  box-sizing: border-box;
}
#root {
  min-height: 100vh;
  width: 100vw;
  background: linear-gradient(120deg, #e0e7ef 0%, #f4f7fb 100%);
}

/* --- APP LAYOUT --- */
.app-container {
  min-height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  background: none;
  padding: 48px 0 80px 0;
  gap: 32px;
}

.main-card {
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 6px 32px #0001, 0 1.5px 8px #7dd3fc22;
  padding: 44px 40px 36px 40px;
  margin: 0 auto;
  width: 100%;
  max-width: 540px;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid #e0e7ef;
}

.main-title {
  font-size: 2.5rem;
  font-weight: 900;
  margin-bottom: 1.2rem;
  color: #2563eb;
  letter-spacing: 0.01em;
  text-align: center;
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
}

.subtitle {
  color: #64748b;
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 2.2rem;
  text-align: center;
}

.file-upload {
  width: 100%;
  margin-bottom: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.file-upload-label {
  display: flex;
  align-items: center;
  gap: 0.7rem;
  background: #e0e7ef;
  border-radius: 10px;
  padding: 0.9rem 1.5rem;
  font-weight: 600;
  color: #2563eb;
  cursor: pointer;
  font-size: 1.1rem;
  border: 1.5px solid #b3b9c9;
  transition: background 0.2s, border 0.2s;
}
.file-upload-label:hover {
  background: #dbeafe;
  border: 1.5px solid #2563eb;
}
.file-upload-input {
  display: none;
}
.file-upload-success {
  margin-top: 0.5rem;
  color: #059669;
  font-weight: 600;
  font-size: 1.05rem;
}

.inputs-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.2rem 2.2rem;
  width: 100%;
  margin-bottom: 1.5rem;
}
.input-label {
  display: flex;
  flex-direction: column;
  font-weight: 600;
  color: #2563eb;
  font-size: 1.05rem;
  gap: 0.3rem;
}
.input-field {
  border: 1.5px solid #b3b9c9;
  border-radius: 8px;
  padding: 10px 14px;
  font-size: 1.08rem;
  background: #f4f7fb;
  color: #1a2233;
  font-family: 'Inter', 'Segoe UI', Arial, sans-serif;
  transition: border 0.2s, background 0.2s, color 0.2s;
}
.input-field:focus {
  border: 1.5px solid #2563eb;
  background: #e0e7ef;
  color: #2563eb;
}

.main-btn {
  background: linear-gradient(90deg, #38bdf8 0%, #2563eb 100%);
  color: #fff;
  font-weight: 800;
  border: none;
  border-radius: 10px;
  padding: 1rem 0;
  font-size: 1.18rem;
  margin: 1.2rem 0 0.5rem 0;
  cursor: pointer;
  box-shadow: 0 2px 8px #2563eb22;
  width: 100%;
  transition: background 0.2s;
  letter-spacing: 0.02em;
}
.main-btn:hover {
  background: linear-gradient(90deg, #2563eb 0%, #38bdf8 100%);
}

.results-section {
  margin-top: 2.2rem;
  width: 100%;
  background: #f4f7fb;
  border-radius: 16px;
  box-shadow: 0 2px 8px #2563eb11;
  padding: 28px 20px 18px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1.5px solid #e0e7ef;
}
.results-title {
  font-size: 1.35rem;
  font-weight: 800;
  margin-bottom: 1.1rem;
  color: #2563eb;
  text-align: center;
}
.results-grid {
  display: flex;
  gap: 1.2rem;
  justify-content: center;
  margin-bottom: 1.2rem;
  flex-wrap: wrap;
}
.result-card {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 1px 4px #2563eb11;
  padding: 1.1rem 1.5rem;
  min-width: 120px;
  text-align: center;
  display: flex;
  flex-direction: column;
  gap: 0.3rem;
  border: 1.5px solid #e0e7ef;
}
.result-label {
  font-size: 1.05rem;
  color: #64748b;
  margin-bottom: 0.2rem;
  font-weight: 600;
}
.result-value {
  font-size: 1.22rem;
  font-weight: 900;
  color: #2563eb;
}

.footer {
  width: 100%;
  text-align: center;
  color: #64748b;
  font-size: 1rem;
  background: none;
  margin-top: 40px;
  padding: 16px 0 0 0;
  border: none;
  position: static;
  padding-bottom: 1.5rem;
}

/* --- MOBILE: MODERN FULL-WIDTH, SINGLE COLUMN, TOUCH OPTIMIZED --- */
@media (max-width: 767px) {
  html, body, #root {
    background: #181c24 !important;
    min-width: 0;
    max-width: 100vw;
    width: 100vw;
    overflow-x: hidden;
    font-size: 16px;
    box-sizing: border-box;
  }
  .app-container, main {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100vw;
    min-width: 0;
    max-width: 100vw;
    padding: 0;
    background: #181c24;
    box-sizing: border-box;
    gap: 0;
  }
  .main-card, .results-section, .file-upload, .inputs-grid {
    width: 100vw !important;
    min-width: 0 !important;
    max-width: 100vw !important;
    border-radius: 0 !important;
    box-shadow: none !important;
    margin: 0 0 18px 0 !important;
    padding: 0 0 0 0 !important;
    background: #232733 !important;
    align-items: stretch !important;
    box-sizing: border-box;
  }
  .main-card {
    padding: 0 0 0 0 !important;
    border: none !important;
  }
  .main-title {
    font-size: 1.35rem !important;
    margin-bottom: 0.7rem !important;
    color: #7dd3fc !important;
    padding: 18px 12px 0 12px !important;
    text-align: left !important;
    font-weight: 900 !important;
    letter-spacing: 0.02em !important;
    background: #232733 !important;
    border-bottom: 1px solid #2d3340 !important;
  }
  .subtitle {
    color: #b3b9c9 !important;
    font-size: 1.01rem !important;
    font-weight: 500 !important;
    margin-bottom: 1.2rem !important;
    padding: 0 12px !important;
    text-align: left !important;
  }
  .file-upload {
    width: 100vw !important;
    padding: 0 12px !important;
    margin-bottom: 0.7rem !important;
    align-items: flex-start !important;
  }
  .file-upload-label {
    width: 100%;
    font-size: 1.08rem !important;
    padding: 14px 14px !important;
    border-radius: 10px !important;
    background: #232733 !important;
    color: #7dd3fc !important;
    border: 1.5px solid #2d3340 !important;
    margin-bottom: 8px !important;
    justify-content: center !important;
  }
  .file-upload-success {
    font-size: 1rem !important;
    margin-top: 4px !important;
    color: #22d3ee !important;
    padding-left: 8px !important;
  }
  .inputs-grid {
    display: flex !important;
    flex-direction: column !important;
    gap: 0.7rem !important;
    width: 100vw !important;
    padding: 0 12px !important;
    background: #232733 !important;
    border-radius: 0 !important;
    border: none !important;
    box-sizing: border-box;
  }
  .input-label {
    font-size: 1.08rem !important;
    color: #7dd3fc !important;
    font-weight: 700 !important;
    margin-bottom: 2px !important;
  }
  .input-field {
    font-size: 1.08rem !important;
    padding: 14px 12px !important;
    border-radius: 10px !important;
    background: #181c24 !important;
    color: #e0e7ef !important;
    border: 1.5px solid #2d3340 !important;
    margin-bottom: 8px !important;
    width: 100% !important;
    box-sizing: border-box;
  }
  .main-btn {
    width: 96vw !important;
    margin: 16px 2vw 0 2vw !important;
    font-size: 1.18rem !important;
    padding: 16px 0 !important;
    border-radius: 12px !important;
    background: linear-gradient(90deg, #7dd3fc 0%, #38bdf8 100%) !important;
    color: #181c24 !important;
    font-weight: 900 !important;
    letter-spacing: 0.03em !important;
    min-height: 52px !important;
    box-sizing: border-box;
    box-shadow: 0 2px 8px #38bdf822 !important;
  }
  .results-section {
    width: 100vw !important;
    max-width: 100vw !important;
    border-radius: 0 !important;
    box-shadow: none !important;
    padding: 10px 0 0 0 !important;
    background: #181c24 !important;
    border: none !important;
    box-sizing: border-box;
  }
  .results-title {
    font-size: 1.18rem !important;
    margin-bottom: 0.7rem !important;
    text-align: left !important;
    padding-left: 12px !important;
    color: #7dd3fc !important;
    font-weight: 900 !important;
  }
  .results-grid {
    flex-direction: column !important;
    gap: 0.7rem !important;
    align-items: stretch !important;
    margin-bottom: 0.7rem !important;
    width: 100vw !important;
    padding: 0 12px !important;
    box-sizing: border-box;
  }
  .result-card {
    min-width: 0 !important;
    width: 100% !important;
    margin: 0 0 8px 0 !important;
    border-radius: 12px !important;
    box-shadow: 0 1px 4px #38bdf822 !important;
    padding: 14px 10px !important;
    font-size: 1.08rem !important;
    background: #232733 !important;
    color: #7dd3fc !important;
    border: 1.5px solid #2d3340 !important;
    text-align: left !important;
    box-sizing: border-box;
  }
  .footer {
    font-size: 1rem !important;
    padding: 12px 0 0 0 !important;
    background: #181c24 !important;
    color: #7dd3fc !important;
    border: none !important;
    margin-top: 24px !important;
    text-align: center !important;
    width: 100vw !important;
    box-sizing: border-box;
  }
  .mobile-nav {
    position: fixed !important;
    bottom: 0 !important;
    left: 0 !important;
    width: 100vw !important;
    background: #232733 !important;
    border-top: 1px solid #2d3340 !important;
    display: flex !important;
    justify-content: space-around !important;
    padding: 8px 0 !important;
    z-index: 100 !important;
    box-sizing: border-box;
  }
  .mobile-nav button, .mobile-nav a {
    background: none !important;
    border: none !important;
    color: #7dd3fc !important;
    font-size: 1.3rem !important;
    padding: 12px !important;
    flex: 1 !important;
    text-align: center !important;
    min-width: 44px !important;
    min-height: 44px !important;
    box-sizing: border-box;
    border-radius: 10px !important;
    transition: background 0.2s;
  }
  .mobile-nav button:active, .mobile-nav button:focus {
    background: #181c24 !important;
    color: #38bdf8 !important;
    outline: none !important;
  }
}

/* --- END --- */
